import React from 'react';

class MovieRow extends React.Component {
  render() {
    return (
      <div key={this.props.movie.id} className="card col-10 col-md-6 m-3 pt-3" style={{ width: '250px' }}>
        <img className="card-img-top" src={this.props.movie.poster_src} alt="Poster" />
        <div className="card-body">
          <h4 className="card-title">{this.props.movie.title}</h4>
          <p className="card-text">{this.props.movie.overview}</p>
          <a href="#index.html" className="btn btn-primary">See Profile</a>
        </div>
      </div>
    );
  }
}

export default MovieRow
